@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@700&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
body::-webkit-scrollbar {
	display: none;
}
body {
	-ms-overflow-style: none;
	scrollbar-width: none;
}

.App{height:100vh}body{background-color:#0d1117;cursor:default}*{transition:all 1s ease;-webkit-user-select:none;-ms-user-select:none;user-select:none;box-sizing:border-box}.fade-enter{opacity:0;z-index:1}.fade-enter.fade-enter-active{opacity:1;transition:opacity 250ms ease-in}.fade-enter{opacity:0.01}.fade-enter.fade-enter-active{opacity:1;transition:opacity 500ms ease-in}.fade-exit{opacity:1}.fade-exit.fade-exit-active{opacity:0.01;transition:opacity 300ms ease-in}

.nav-elements{font-size:1.1rem;margin:auto;padding:0 10px;color:#fff;text-decoration:none}.nav-elements:hover{color:#1f6feb;font-weight:bolder}.active{color:#1f6feb;pointer-events:none;cursor:default;font-weight:bolder}

.Nav{background-color:transparent;justify-content:center;padding:0 10%;padding-top:24px;font-family:'Lato', sans-serif}@media (min-width: 1030px){.Nav .container{width:70%}}

#intro{background-color:#0d1117;display:flex;height:100vh;flex-basis:auto}@media (max-width: 830px) and (min-width: 320px){#intro{margin-top:70px;flex-wrap:wrap-reverse;height:auto}}

#introText{display:flex;width:100%;flex-direction:column;margin:auto 0;margin-left:10%;font-family:PT Sans;font-size:4rem;color:#fff}#introText .typewriter{display:flex}#introText .typewriter .designation{margin-left:20px;min-width:340px;font-family:Roboto Slab;color:#1f6feb}@media (max-width: 520px){#introText{font-size:3rem}}.name{padding:0 10px;background-color:#fff;border-radius:20px;font-family:Merriweather;color:#0d1117;transition:all 0.2s ease-in}.name:hover{color:#fff;background-color:#1f6feb}

#photo{display:flex;align-items:center;width:100%;margin:0 0;padding:0 0;overflow-y:hidden;overflow-x:hidden}#photo img{margin:auto}@media screen and (min-width: 1000px){#photo img{height:100vh}}@media screen and (max-width: 830px){#photo img{border-radius:500px}}

#about{background-color:#0d1117;height:100vh;display:flex;flex-basis:auto;padding-top:30px}@media (max-width: 930px) and (min-width: 320px){#about{margin-top:70px}#about .image-container{display:none}#about .content-wrapper #content{margin:auto 20px !important}}

.content-wrapper{display:flex;justify-content:center;align-items:center}#content{color:#0d1117;background-color:#fff;margin:0 10% 0 30%;padding:30px;text-align:justify;border-radius:50px 0px 50px 0px;font-weight:bold}

.image-container{display:flex;width:50%}

#workPage{display:flex;flex-direction:column;height:100vh;padding-top:55px;overflow-y:visible}#workPage ::-webkit-scrollbar{display:block}#workPage .heading{width:100%;text-align:center;color:#fff;font-size:4rem;justify-content:center;display:flex;flex-direction:column;align-items:center;font-weight:bold;font-family:'Open Sans Condensed', sans-serif}#workPage section{display:flex}


