x -8px rgba(0,0,0,0.025);padding:40px;height:40px;position:relative}.timeline__event__icon i{font-size:32px}.timeline__event__icon:before{content:'';width:2px;height:100%;background:#f6a4ec;position:absolute;top:0%;z-index:-1;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);-webkit-animation:fillTop 2s forwards 4s ease-in-out;animation:fillTop 2s forwards 4s ease-in-out}.timeline__event__icon:after{content:'';width:100%;height:2px;background:#f6a4ec;position:absolute;left:0%;z-index:-1;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);-webkit-animation:fillLeftOdd 2s forwards 4s ease-in-out;animation:fillLeftOdd 2s forwards 4s ease-in-out}.timeline__event__description{flex-basis:60%}.timeline__event--type2:after{background:#555ac0}.timeline__event--type2 .timeline__event__date{color:#87bbfe;background:#555ac0}.timeline__event--type2:nth-child(2n+1) .timeline__event__icon:before,.timeline__event--type2:nth-child(2n+1) .timeline__event__icon:after{background:#87bbfe}.timeline__event--type2 .timeline__event__icon{background:#87bbfe;color:#555ac0}.timeline__event--type2 .timeline__event__icon:before,.timeline__event--type2 .timeline__event__icon:after{background:#87bbfe}.timeline__event--type2 .timeline__event__title{color:#555ac0}.timeline__event--type3:after{background:#24b47e}.timeline__event--type3 .timeline__event__date{color:#aff1b6;background-color:#24b47e}.timeline__event--type3:nth-child(2n+1) .timeline__event__icon:before,.timeline__event--type3:nth-child(2n+1) .timeline__event__icon:after{background:#aff1b6}.timeline__event--type3 .timeline__event__icon{background:#aff1b6;color:#24b47e}.timeline__event--type3 .timeline__event__icon:before,.timeline__event--type3 .timeline__event__icon:after{background:#aff1b6}.timeline__event--type3 .timeline__event__title{color:#24b47e}.timeline__event:last-child .timeline__event__icon:before{content:none}@media (max-width: 786px){.timeline__event{flex-direction:column;align-self:center}.timeline__event__content{width:100%}.timeline__event__icon{border-radius:6px 6px 0 0;width:100%;margin:0;box-shadow:none}.timeline__event__icon:before,.timeline__event__icon:after{display:none}.timeline__event__date{border-radius:0;padding:20px}.timeline__event:nth-child(2n+1){flex-direction:column;align-self:center}.timeline__event:nth-child(2n+1) .timeline__event__date{border-radius:0;padding:20px}.timeline__event:nth-child(2n+1) .timeline__event__icon{border-radius:6px 6px 0 0;margin:0}}@-webkit-keyframes fillLeft{100%{right:100%}}@keyframes fillLeft{100%{right:100%}}@-webkit-keyframes fillTop{100%{top:100%}}@keyframes fillTop{100%{top:100%}}@-webkit-keyframes fillLeftOdd{100%{left:100%}}@keyframes fillLeftOdd{100%{left:100%}}

#contact{height:100vh;padding-top:100px;display:flex;flex-wrap:wrap-reverse}#contact .contact-form{flex-grow:1.3;display:flex}#contact .contact-form .inputs{display:flex;flex-direction:column;height:100%;justify-content:center;margin:0 auto;width:70%}#contact .contact-form .inputs span{display:flex;flex-wrap:wrap}#contact .contact-form .inputs span .box{flex-grow:1}#contact .contact-form .inputs .box{position:relative;display:inline-block;margin:0 10px}#contact .contact-form .inputs .box input{width:100%;padding:10px 0;font-size:18px;color:#fff;margin-bottom:30px;border:none;border-bottom:1px solid #1f6feb;outline:none;background:transparent}#contact .contact-form .inputs .box .label{position:absolute;top:0;left:0;padding:10px 0;font-size:16px;color:#1f6feb;pointer-events:none;transition:0.5s}#contact .contact-form .inputs .box .active-label{top:-25px !important;left:0 !important;font-size:12px !important}#contact .contact-form .inputs textarea{padding:10px 0;font-size:18px;color:#fff;margin:0 auto;margin-bottom:30px;border:none;border-bottom:1px solid #1f6feb;outline:none;background:transparent;width:97%;height:100px;overflow-y:hidden;max-height:300px}#contact .contact-form .inputs button{border-radius:15px;background-color:#1f6feb;color:#fff;padding:10px 20px;width:-webkit-max-content;width:max-content;margin-right:20px}#contact .contact-form .inputs button:hover{background-color:#fff;color:#1f6feb}#contact .contact-form .inputs .social-media{margin-top:20px;padding:0 10px;display:flex;flex-wrap:reverse}#contact .contact-form .inputs .social-media a{background-color:#fff;font-size:40px;height:60px;width:60px;text-align:center;border-radius:20px;margin:10px 20px 0 0}@media (max-width: 768px){#contact .contact-form .inputs{width:90%}}#contact .contact-image-holder{flex-grow:1;display:flex;height:100%;justify-content:center;align-items:center}#contact .contact-image-holder span object{width:100%}@media (min-width: 768px){#contact .contact-image-holder span object{width:500px}}@media (max-width: 768px){#contact .contact-image-holder{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}}

