#photo {
	display: flex;
	align-items: center;
	width: 100%;
	margin: 0 0;
	padding: 0 0;
	overflow-y: hidden;
	overflow-x: hidden;

	img {
		margin: auto;
		@media screen and (min-width: 1000px) {
			height: 100vh;
		}
		@media screen and (max-width: 830px) {
			border-radius: 500px;
		}
	}
}
